.Movie{
   width : 40%;
   display: flex;
   align-items: flex-start;
   flex-wrap: wrap;
   padding: 0 20px;
   margin-bottom: 30px;
   border : 1px solid lightgray;
   box-shadow: 3px 3px 3px 1px lightgray;
}

.Movie__Column {
    width: 40%;
}

.Movie__Column :last-child{
    width: 60%;
}

.Movie .Movie__Poster{
    box-shadow: 3px 3px 2px 1px lightgray;
}

.Movie .Movie__Genres{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
}